// ** Redux Imports
import { createSlice, createAsyncThunk } from "@reduxjs/toolkit"
import useJwt from "@src/auth/jwt/useJwt"

export const getData = createAsyncThunk("role/getData", async (params) => {
  const response = await useJwt.ssoAxiosInterceptor.get("/api/role", {
    params
  })
  return {
    data: response.data.data
  }
})

export const getSummaryData = createAsyncThunk(
  "role/getSummaryData",
  async (params) => {
    Object.keys(params).forEach((key) => {
      if (params[key] === null || params[key] === "") {
        delete params[key]
      }
    })
    const response = await useJwt.ssoAxiosInterceptor.get("/api/role/summary", {
      params
    })
    return {
      params,
      data: response.data.data
    }
  }
)

export const addRole = createAsyncThunk(
  "roles/addRole",
  async (role, { dispatch, getState }) => {
    await useJwt.ssoAxiosInterceptor.post("/api/role", { role })
    await dispatch(getData(getState().roles.params))
    return role
  }
)

export const updateRole = createAsyncThunk(
  "roles/updateRole",
  async ({ id, name }, { dispatch, getState }) => {
    await useJwt.ssoAxiosInterceptor.post("/apps/roles/update-role", {
      id,
      name
    })
    await dispatch(getData(getState().roles.params))
    return { id, name }
  }
)

export const deleteRole = createAsyncThunk(
  "roles/deleteRole",
  async (id, { dispatch, getState }) => {
    await useJwt.ssoAxiosInterceptor.delete("/apps/roles/delete", { id })
    await dispatch(getData(getState().roles.params))
    return id
  }
)
const fetchStatus = "idle" | "loading" | "succeeded" | "failed"
export const rolesSlice = createSlice({
  name: "roles",
  initialState: {
    crudTitle: "Roles",
    // SUMMARY
    statusSummary: fetchStatus,
    isLoadingSummary: false,
    errorSummary: "" | null,
    dataSummary: [],

    // ALL
    status: fetchStatus,
    isLoading: false,
    error: "" | null,
    data: [],

    selected: null
  },
  reducers: {
    selectRole: (state, action) => {
      if (action.payload === null) {
        state.selected = null
      } else {
        state.selected = action.payload
      }
    }
  },
  extraReducers: (builder) => {
    builder
      ///ALL
      .addCase(getData.pending, (state) => {
        state.status = "loading"
        state.isLoading = true
      })
      .addCase(getData.fulfilled, (state, action) => {
        state.status = "succeeded"
        state.isLoading = false
        state.data = action.payload.data
      })
      .addCase(getData.rejected, (state, action) => {
        state.status = "failed"
        state.isLoading = false
        state.error = action.error.message
      })

      ///SUMMARY
      .addCase(getSummaryData.pending, (state) => {
        state.statusSummary = "loading"
        state.isLoadingSummary = true
      })
      .addCase(getSummaryData.fulfilled, (state, action) => {
        // console.log(action.payload)
        state.statusSummary = "succeeded"
        state.isLoadingSummary = false
        state.dataSummary = action.payload.data
      })
      .addCase(getSummaryData.rejected, (state, action) => {
        state.statusSummary = "failed"
        state.isLoadingSummary = false
        state.errorSummary = action.error.message
      })
  }
})

export const { selectRole } = rolesSlice.actions

export default rolesSlice.reducer
