export const getSummaryData = createAsyncThunk(
  "appUsers/getSummaryData",
  async (params) => {
    Object.keys(params).forEach((key) => {
      if (params[key] === null || params[key] === "") {
        delete params[key]
      }
    })
    const response = await useJwt.ssoAxiosInterceptor.get("/api/user/summary", {
      params
    })
    return {
      params,
      data: response.data.data
    }
  }
)

export const getData = createAsyncThunk("appUsers/getData", async (params) => {
  Object.keys(params).forEach((key) => {
    if (params[key] === null || params[key] === "") {
      delete params[key]
    }
  })
  const response = await useJwt.ssoAxiosInterceptor.get("/api/user", {
    params
  })
  return {
    params,
    data: response.data.data,
    totalPages: response.data.total
  }
})

export const getUser = createAsyncThunk("appUsers/getUser", async (id) => {
  const response = await useJwt.ssoAxiosInterceptor.get(`/api/user/${id}`)
  // console.log(response)
  return response.data.data
})

export const addUser = createAsyncThunk(
  "appUsers/addUser",
  async (user, { dispatch, getState, rejectWithValue }) => {
    try {
      const response = await useJwt.ssoAxiosInterceptor.post("/api/user", user)
      await dispatch(getData(getState().users.params))
      dispatch(resetDataRole({}))
      // return response.data
      return user
    } catch (error) {
      return rejectWithValue(error.response.data)
    }
  }
)

export const updateUser = createAsyncThunk(
  "appUsers/updateUser",
  async (data, { dispatch, getState, rejectWithValue }) => {
    const id = data.id
    delete data.id
    try {
      const response = await useJwt.ssoAxiosInterceptor.put(
        `/api/user/${id}`,
        data
      )
      dispatch(getUser(id))
      dispatch(getData(getState().users.params))
      dispatch(getSummaryData({}))
      dispatch(resetDataRole({}))
      return response.data
    } catch (error) {
      return rejectWithValue(error.response.data.errors)
    }
  }
)

export const deleteUser = createAsyncThunk(
  "appUsers/deleteUser",
  async (id, { dispatch, getState, rejectWithValue }) => {
    try {
      const response = await useJwt.ssoAxiosInterceptor.delete(
        `/api/user/${id}`
      )
      await dispatch(getData(getState().users.params))
      dispatch(getSummaryData({}))
      dispatch(resetDataRole({}))
      return response.data
    } catch (error) {
      return rejectWithValue(error.response.data)
    }
  }
)

export const getDataEmployee = createAsyncThunk(
  "appUsers/getDataEmployee",
  async (params) => {
    Object.keys(params).forEach((key) => {
      if (params[key] === null || params[key] === "") {
        delete params[key]
      }
    })
    const response = await useJwt.hrAxiosInterceptor.get("/Employee", {
      params
    })
    return {
      params,
      data: response.data.data,
      totalPages: response.data.total
    }
  }
)

const fetchStatus = "idle" | "loading" | "succeeded" | "failed"

export const appUsersSlice = createSlice({
  name: "appUsers",
  initialState: {
    crudTitle: "User",
    // SUMMARY
    statusSummary: fetchStatus,
    isLoadingSummary: false,
    errorSummary: "" | null,
    dataSummary: [],

    // ALL
    status: fetchStatus,
    isLoading: false,
    error: "" | null,
    data: [],
    total: 1,
    params: {},

    // EMPLOYEE
    status_employee: fetchStatus,
    isLoading_employee: false,
    error_employee: "" | null,
    data_employee: [],
    total_employee: 1,
    params_employee: {},

    // DETAIL
    statusDetail: fetchStatus,
    isLoadingDetail: false,
    errorDetail: "" | null,
    selectedUser: null,

    // ADD&EDIT
    statusAddEdit: fetchStatus,
    isLoadingAddEdit: false,
    errorAddEdit: "" | null,

    // ADD&EDIT
    statusDelete: fetchStatus,
    isLoadingDelete: false,
    errorDelete: "" | null
  },
  reducers: {},
  extraReducers: (builder) => {
    builder
      ///SUMMARY
      .addCase(getSummaryData.pending, (state) => {
        state.statusSummary = "loading"
        state.isLoadingSummary = true
      })
      .addCase(getSummaryData.fulfilled, (state, action) => {
        // console.log(action.payload)
        state.statusSummary = "succeeded"
        state.isLoadingSummary = false
        state.dataSummary = action.payload.data
      })
      .addCase(getSummaryData.rejected, (state, action) => {
        state.statusSummary = "failed"
        state.isLoadingSummary = false
        state.errorSummary = action.error.message
      })

      ///ALL
      .addCase(getData.pending, (state) => {
        state.status = "loading"
        state.isLoading = true
      })
      .addCase(getData.fulfilled, (state, action) => {
        // console.log(action.payload)
        state.status = "succeeded"
        state.isLoading = false
        state.data = action.payload.data
        state.params = action.payload.params
        state.total = action.payload.totalPages
      })
      .addCase(getData.rejected, (state, action) => {
        state.status = "failed"
        state.isLoading = false
        state.error = action.error.message
      })

      ///ALL
      .addCase(getDataEmployee.pending, (state) => {
        state.status_employee = "loading"
        state.isLoading_employee = true
      })
      .addCase(getDataEmployee.fulfilled, (state, action) => {
        // console.log(action.payload)
        state.status_employee = "succeeded"
        state.isLoading_employee = false
        state.data_employee = action.payload.data
        state.params_employee = action.payload.params
        state.total_employee = action.payload.totalPages
      })
      .addCase(getDataEmployee.rejected, (state, action) => {
        state.status_employee = "failed"
        state.isLoading_employee = false
        state.error_employee = action.error.message
      })

      ///EDIT
      .addCase(updateUser.pending, (state) => {
        state.statusAddEdit = "loading"
        state.isLoadingAddEdit = true
      })
      .addCase(updateUser.fulfilled, (state) => {
        // console.log(action.payload)
        state.statusAddEdit = "succeeded"
        state.isLoadingAddEdit = false
      })
      .addCase(updateUser.rejected, (state, action) => {
        state.statusAddEdit = "failed"
        state.isLoadingAddEdit = false
        if (typeof action.payload === "object") {
          state.errorAddEdit = JSON.stringify(action.payload)
        } else {
          state.errorAddEdit = action.payload.message
        }
      })

      ///ADD
      .addCase(addUser.pending, (state) => {
        state.statusAddEdit = "loading"
        state.isLoadingAddEdit = true
      })
      .addCase(addUser.fulfilled, (state) => {
        // console.log(action.payload)
        state.statusAddEdit = "succeeded"
        state.isLoadingAddEdit = false
      })
      .addCase(addUser.rejected, (state, action) => {
        console.log(action)

        if (action.payload.errors) {
          state.errorAddEdit = Object.values(action.payload.errors)
        } else if (action.payload.error_reason) {
          state.errorAddEdit = action.payload.message
        }
        state.statusAddEdit = "failed"
        state.isLoadingAddEdit = false
      })

      ///DELETE
      .addCase(deleteUser.pending, (state) => {
        state.statusDelete = "loading"
        state.isLoadingDelete = true
      })
      .addCase(deleteUser.fulfilled, (state) => {
        // console.log(action.payload)
        state.statusDelete = "succeeded"
        state.isLoadingDelete = false
      })
      .addCase(deleteUser.rejected, (state, action) => {
        state.statusDelete = "failed"
        state.isLoadingDelete = false
        // console.log(action)
        state.errorDelete = action.payload.message
      })

      ///DETAIL
      .addCase(getUser.pending, (state) => {
        state.statusDetail = "loading"
        state.isLoadingDetail = true
        state.selectedUser = null
      })
      .addCase(getUser.fulfilled, (state, action) => {
        state.statusDetail = "succeeded"
        state.isLoadingDetail = false
        state.selectedUser = action.payload
      })
      .addCase(getUser.rejected, (state, action) => {
        state.statusDetail = "failed"
        state.isLoadingDetail = false
        state.selectedUser = null
        state.errorDetail = action.error.message
      })
  }
})

export default appUsersSlice.reducer
